.landing-page {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.landing-page h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.landing-page p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  text-align: center;
}

.landing-page button {
  padding: 1rem 2rem;
  background-color: #4CAF50;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.landing-page button:hover {
  background-color: #45a049;
}

.landing-page .company-description {
  max-width: 800px; 
  margin: 0 auto 2rem; 
  text-align: center; 
  font-size: 1.2rem; 
  line-height: 1.8; 
  color: #f5f5f5; 
}
